.main-nav {
  position: relative;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  text-align: center;
  border-bottom: 1px solid #e6e6e6;
}
.main-nav__link {
  position: relative;
  display: block;
  padding: 20px;
  font-family: inherit;
  color: inherit;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
}
.main-nav__link:not(.main-nav__link--current):hover {
  opacity: 0.6;
}
.main-nav__link:not(.main-nav__link--current):active {
  opacity: 0.3;
}
.main-nav__toggle {
  position: absolute;
  top: -50px;
  right: 50%;
  margin-right: -143px;
  padding: 0;
  display: block;
  width: 30px;
  height: 30px;
  font-size: 0;
  background-color: #ffffff;
  border: 0;
  cursor: pointer;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 7px;
  left: 2px;
  width: 25px;
  height: 2px;
  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 15px;
  left: 2px;
  width: 25px;
  height: 2px;
  background-color: #222222;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__list {
    margin: 0 auto;
  }
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--nojs .main-nav__list {
    display: block;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
  }
  .main-nav__item {
    border-bottom: none;
  }
  .main-nav__item:not(:nth-child(3)) {
